body{
    padding: 0;
    margin: 0;
    font-family: 'Open Sans', sans-serif;
    font-weight: 400;
    font-size: 12px;
    line-height: 19px;
}

.blogpost {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    border: 1px solid #dddddd;
    border-radius: 5px;
    width: 302px;
    height: auto;
    padding: 4px;
    color: #6c6c6c;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;

    h3 {
        margin: 14px 0 0 0;
        font-size: 15px;
        font-weight: 600;
        line-height: 16px;
    }

    img {
        object-fit: cover;
        width: 292px;
        height: 217px;
        //max-width: 100%;
        //max-height: 100%;
        border-radius: 4px;
    }

    p {
        margin: 0;
    }

    .separator {
        width: 80px;
        margin: 16px 0 9px 0;
        border-top: 1px solid #d5d5d5;
    }

    .blogpost-text {
        padding: 15px 15px 23px;
    }
}

.masonry {
    display: flex;
    align-items: flex-start;
    gap: 18px;
    flex-flow: wrap;
    margin: auto;
    //width: 100%;
    width: 942px;
}


.header-top {
    padding: 0;
    margin: 0;
    height: 78px;
    background: #333333;

    .header-top-inside {
        display: flex;
        width: 942px;
        height: 100%;
        margin: auto;
    }
}

.logo {
    font-family: "Wolf Sans Regular", sans-serif;
    font-size: 38px;
    width: 170px;
    padding: 30px 0;
    text-align: center;
    color: #ffffff;
    background: #ff5656;
}

.circle {
    width: 27px;
    height: 27px;
    color: #333333;
    background: #b6b6b6;
    border-radius: 50%;
    display: inline-block;
    text-align: center;

    i {
        margin-left: 1px;
        position: relative;
        top: 20%;
        font-size: 14px;
    }

    &:hover {
        background: #d9d9d9;
    }

    &:active {
        background: #d9d9d9;
    }
}

.social-media-bar {
    margin: auto 0 auto auto;
    display: inline-flex;
    gap: 8px;
    height: 30px;
}


.header-middle {
    display: flex;
    height: 124px;
    width: 942px;
    margin: auto;
    background: #ffffff;
    p {
        color: #4a4a4a;
        font-size: 24px;
        margin: auto 0 auto 0;
    }
}

.header-bottom {
    border-top: 1px solid #d5d5d5;
    border-bottom: 1px solid #d5d5d5;
    -webkit-box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.20);
    -moz-box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.20);
    box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.20);
}

.navbar {
    width: 942px;
    height: 62px;
    margin: auto;
    overflow: auto;
    display: flex;
    border-right: 1px solid #d5d5d5;

    a.navlink {
        font-size: 17px;
        display: inline-block;
        padding: 21px 20px;
        background: white;
        margin: -1px -1px -1px 0;
        text-decoration: none;
        border: 1px solid #d5d5d5;
    }

    a.navlink:visited {
        color: #6c6c6c;
    }
}

.search-box-wrapper {
    margin: auto 14px auto auto;
    display: inline;
}

.search-box {
    border: 1px solid #d5d5d5;
    border-radius: 5px;
    width: 221px;
    height: 33px;
}


@font-face {
    font-family:"Wolf Sans Regular";
    src: url("../fonts/Wolf_Sans_Regular.eot?") format("eot"),
         url("../fonts/Wolf_Sans_Regular.woff") format("woff"),
         url("../fonts/Wolf_Sans_Regular.ttf") format("truetype"),
         url("../fonts/Wolf_Sans_Regular.svg#WolfSans-Regular") format("svg");
    font-weight: normal;
    font-style: normal;
}

